import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

/* eslint-disable global-require, import/no-dynamic-require */
const router = new VueRouter({
  mode: 'history',
  base: '/image-resize',
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: '/resize-demo',
      name: 'resizeDemo',
      component: resolve => require(['../pages/content/demo'], resolve),
      meta: {
        title: 'demo',
      },
    },
  ],
});

export default router;



