export default {
  install (Vue) {
    let resizeSupported = () => {
      let canvas = document.createElement('canvas')
      return canvas.getContext && canvas.getContext('2d') && window.Blob && window.atob
    }

    let dataUrl = (file) => {
      return new Promise((resolve, reject) => {
        if (file.type.indexOf('image') !== 0) {
          reject(new Error('图片格式非法'))
        }
        let fileReader = new FileReader()
        fileReader.onload = function (e) {
          resolve(e.target.result)
        }
        fileReader.onerror = function () {
          reject(new Error('图片读取失败'))
        }
        fileReader.readAsDataURL(file)
      })
    }

    let getImageDimensions = (file) => {
      return new Promise((resolve, reject) => {
        dataUrl(file).then((url) => {
          let img = new Image()
          img.src = url
          img.onload = () => {
            resolve({
              width: img.width,
              height: img.height
            })
          }
          img.onerror = () => {
            reject(new Error('图片读取失败'))
          }
        })
      })
    }

    let calculateAspectRatioFit = (srcWidth, srcHeight, maxWidth, maxHeight, centerCrop) => {
      let ratio = centerCrop ? Math.max(maxWidth / srcWidth, maxHeight / srcHeight) : Math.min(maxWidth / srcWidth, maxHeight / srcHeight)
      return {
        width: srcWidth * ratio,
        height: srcHeight * ratio,
        marginX: srcWidth * ratio - maxWidth,
        marginY: srcHeight * ratio - maxHeight
      }
    }

    let resizeDataUrl = ({url, width, height, quality, type, ratio, centerCrop}) => {
      return new Promise((resolve, reject) => {
        let canvasElement = document.createElement('canvas')
        let imageElement = document.createElement('img')
        imageElement.setAttribute('style', 'visibility:hidden;position:fixed;z-index:-100000')
        document.body.appendChild(imageElement)
        imageElement.onload = () => {
          let imgWidth = imageElement.width
          let imgHeight = imageElement.height
          imageElement.parentNode.removeChild(imageElement)
          try {
            if (ratio) {
              ratio = parseFloat(ratio)
              let imgRatio = imgWidth / imgHeight
              if (imgRatio < ratio) {
                width = imgWidth
                height = width / ratio
              } else {
                height = imgHeight
                width = height * ratio
              }
            }
            !width && (width = imgWidth)
            !height && (height = imgHeight)
            quality && (quality = parseFloat(quality))
            let dimensions = calculateAspectRatioFit(imgWidth, imgHeight, width, height, centerCrop)
            canvasElement.width = Math.min(dimensions.width, width)
            canvasElement.height = Math.min(dimensions.height, height)
            let context = canvasElement.getContext('2d')
            context.drawImage(imageElement,
              Math.min(0, -dimensions.marginX / 2), Math.min(0, -dimensions.marginY / 2),
              dimensions.width, dimensions.height)
            resolve(canvasElement.toDataURL(type || 'image/jpeg', quality || 0.934))
          } catch (e) {
            reject(new Error('图片压缩失败'))
          }
        }
        imageElement.src = url
      })
    }

    let dataUrltoBlob = (dataurl, name, origSize) => {
      return new Promise((resolve, reject) => {
        let arr = dataurl.split(',')
        let mime = arr[0].match(/:(.*?);/)[1]
        let bstr = atob(arr[1])
        let n = bstr.length
        let u8arr = new Uint8Array(n)
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n)
        }
        try {
          let blob = new window.Blob([u8arr], {type: mime})
          blob.name = name
          resolve({
            blob: blob,
            dataurl: dataurl
          })
        } catch (e) {
          reject(new Error('图片atob失败'))
        }
      })
    }

    let resizeImage = (file, options) => {
      return new Promise((resolve, reject) => {
        if (file.type.indexOf('image') !== 0) reject(new Error('图片格式非法'))
        dataUrl(file).then((url) => {
          if (!options) {
            options = {}
          }
          options.url = url
          resizeDataUrl(options).then((rUrl) => {
            dataUrltoBlob(rUrl, file.name, file.size).then((result) => {
              resolve(result)
            }).catch((e) => {
              reject(e)
            })
          }).catch((e) => {
            reject(e)
          })
        }).catch((e) => {
          reject(e)
        })
      })
    }
    Vue.prototype.$resizeSupported = resizeSupported
    Vue.$resizeSupported = resizeSupported

    Vue.prototype.$dataUrl = dataUrl
    Vue.$dataUrl = dataUrl

    Vue.prototype.$imgDimension = getImageDimensions
    Vue.$imgDimension = getImageDimensions

    Vue.prototype.$resizeDataUrl = resizeDataUrl
    Vue.$resizeDataUrl = resizeDataUrl

    Vue.prototype.$dataUrlToBlob = dataUrltoBlob
    Vue.$dataUrlToBlob = dataUrltoBlob

    Vue.prototype.$resizeImage = resizeImage
    Vue.$resizeImage = resizeImage
  }
}



